:root {
    /* Light mode (default) */
    --primary-color: #3a6ea5;
    --secondary-color: #3a6ea5;
    --accent-color: #4fc3f7;
    --light-bg: #f7f9fc;
    --dark-text: #2d3748;
    --light-text: #4a5568;
    --very-light-text: #a0aec0;
    --border-color: #e2e8f0;
    --shadow-color: rgba(0, 0, 0, 0.06);
    --completed-color: #68d391;
    --card-bg: #ffffff;
    --header-text: #ffffff;
    --input-bg: #ffffff;
    --input-text: #2d3748;
    --input-placeholder: #a0aec0;
    --input-border: #e2e8f0;
    --input-focus-border: #3a6ea5;
    --input-focus-shadow: rgba(58, 110, 165, 0.15);
    --success-color: #68d391;
    --warning-color: #f6ad55;
    --danger-color: #fc8181;
}

/* Dark mode variables */
html.dark-mode {
    --primary-color: #246fac;
    --secondary-color: #246fac;
    --accent-color: #1d85da;
    --light-bg: #121212;
    --dark-text: #e4e6eb;
    --light-text: #b0b3b8;
    --very-light-text: #777a7e;
    --border-color: #2c2d2f;
    --shadow-color: rgba(0, 0, 0, 0.3);
    --completed-color: #43a047;
    --card-bg: #1e1e1e;
    --header-text: #ffffff;
    --input-bg: #2c2d2f;
    --input-text: #e4e6eb;
    --input-placeholder: #777a7e;
    --input-border: #3c3d3f;
    --input-focus-border: #64b5f6;
    --input-focus-shadow: rgba(100, 181, 246, 0.2);
    --success-color: #43a047;
    --warning-color: #ff9800;
    --danger-color: #f44336;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
    background-color: var(--light-bg);
    color: var(--dark-text);
    line-height: 1.6;
    display: flex;
    min-height: 100vh;
    font-size: 15px;
    transition: background-color 0.3s ease, color 0.3s ease;
}

#root {
    width: 100%;
}

.app-container {
    display: flex;
    width: 100%;
    min-height: 100vh;
}














/* Sidebar Styles */
.sidebar {
    width: 280px;
    background: var(--card-bg);
    border-right: 1px solid var(--border-color);
    display: flex;
    flex-direction: column;
    box-shadow: 2px 0 10px var(--shadow-color);
    position: fixed;
    height: 100vh;
    z-index: 1000;
    transition: transform 0.3s ease;
}

.sidebar-header {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: var(--header-text);
    padding: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
}

.brand {
    display: flex;
    align-items: center;
    gap: 12px;
    font-weight: 700;
    letter-spacing: .3px;
    font-size: 18px;
}

.brand-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    background: rgba(255, 255, 255, .25);
    box-shadow: inset 0 1px 3px rgba(0, 0, 0, .15);
}

.sidebar-nav {
    padding: 20px 0;
    flex: 1;
}

.nav-item {
    margin: 0 16px 8px 16px;
}

.nav-link {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px 16px;
    border-radius: 8px;
    color: var(--dark-text);
    text-decoration: none;
    font-weight: 500;
    transition: all .2s ease;
    border: 1px solid transparent;
}

.nav-link:hover {
    background: var(--light-bg);
    color: var(--primary-color);
    border-color: var(--border-color);
}

.nav-link.active {
    background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
    color: var(--header-text);
}

.nav-link.active:hover {
    background: linear-gradient(135deg, var(--secondary-color), var(--primary-color));
}

.nav-icon {
    width: 20px;
    height: 20px;
    opacity: .8;
}

.sidebar-footer {
    padding: 20px;
    border-top: 1px solid var(--border-color);
    background: var(--light-bg);
}

.user-info {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 16px;
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--primary-color);
    overflow: hidden;
    border: 2px solid var(--border-color);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.user-details {
    flex: 1;
}

.user-name {
    font-weight: 600;
    color: var(--dark-text);
    font-size: 14px;
}

.storage-badge {
    font-size: 11px;
    color: var(--light-text);
    margin-top: 2px;
}

/* --- Small Sidebar Styles --- */
.sidebar.small {
    width: 88px;
}

.sidebar.small .brand > div:not(.brand-logo) {
    display: none;
}

.sidebar.small .brand {
    justify-content: center;
}

.sidebar.small .nav-link {
    justify-content: center;
}

.sidebar.small .nav-link > span {
    display: none;
}

.sidebar.small .user-details,
.sidebar.small .storage-badge {
    display: none;
}

.sidebar.small .user-info {
    justify-content: center;
    position: relative;
    margin-bottom: 0;
}

.sidebar-toggle-btn {
    position: absolute;
    top: 20px;
    right: 0;
    transform: translateX(50%);
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    box-shadow: 0 2px 4px var(--shadow-color);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--light-text);
    transition: all 0.2s ease;
}


/* Main Content Area */
.main-content {
    flex: 1;
    margin-left: 280px;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    transition: margin-left 0.3s ease;
}

.main-content.sidebar-small {
    margin-left: 88px;
}

.content-header {
    background: var(--card-bg);
    padding: 20px 30px;
    border-bottom: 1px solid var(--border-color);
    box-shadow: 0 2px 4px var(--shadow-color);
    position: sticky; top: 0; z-index: 900;
}

.page-title {
    font-size: 24px;
    font-weight: 700;
    color: var(--dark-text);
    margin: 0;
}

.page-subtitle {
    color: var(--light-text);
    margin-top: 4px;
    font-size: 14px;
}

.page-title-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
}

.content {
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 24px;
    flex: 1;
}

.header-actions {
    display: flex;
    align-items: center;
    gap: 16px;
}

.header-icon-btn {
    background: transparent;
    border: none;
    color: var(--light-text);
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
}

.header-icon-btn:hover {
    background-color: var(--light-bg);
    color: var(--primary-color);
}

.header-avatar {
    width: 36px;
    height: 36px;
}

/* General Components */
.btn {
    background: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 12px 18px;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(58, 110, 165, .2);
    font-size: 14px;
    transition: all .2s ease;
    display: inline-flex;
    align-items: center;
    gap: 8px;
    justify-content: center;
}

.btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(58, 110, 165, .3);
}

.btn.secondary {
    background: transparent;
    color: var(--primary-color);
    border: 1px solid var(--primary-color);
}

.btn.secondary:hover {
    background: var(--primary-color);
    color: #fff;
}

.btn.danger {
    background: var(--danger-color);
}

.btn.danger:hover {
    background: #e53e3e;
}

.btn.danger-outline {
    background: transparent;
    color: var(--danger-color);
    border: 1px solid var(--danger-color);
}

.btn.danger-outline:hover {
    background: var(--danger-color);
    color: #fff;
}

.project-idea-form {
    display: flex;
    flex-direction: column;
}

.idea-textarea {
    min-height: 120px;
    margin-bottom: 16px;
}

.idea-form-actions {
    display: flex;
    gap: 12px;
    justify-content: flex-end;
}

.project-idea-display {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 24px;
    background-color: var(--light-bg);
    padding: 16px;
    border-radius: 8px;
}

.project-idea-display p {
    flex: 1;
    margin: 0;
    line-height: 1.6;
    white-space: pre-wrap; /* Respects newlines in the text */
}

.idea-display-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
}

.text-input {
    flex: 1;
    padding: 12px 16px;
    border-radius: 8px;
    border: 1px solid var(--input-border);
    background: var(--input-bg);
    color: var(--input-text);
    font-size: 14px;
}

.text-input:focus {
    outline: none;
    border-color: var(--input-focus-border);
    box-shadow: 0 0 0 3px var(--input-focus-shadow);
}

textarea.text-input {
    resize: vertical;
    min-height: 80px;
    line-height: 1.5;
}

.section {
    border: 1px solid var(--border-color);
    border-radius: 12px;
    padding: 24px;
    background: var(--card-bg);
    box-shadow: 0 2px 6px var(--shadow-color);
}

.section h3 {
    font-size: 18px;
    color: var(--primary-color);
    margin-bottom: 16px;
    font-weight: 600;
}

/* Mobile Menu Toggle */
.mobile-menu-toggle {
    display: none;
    position: fixed;
    top: 20px;
    left: 20px;
    z-index: 1001;
    background: var(--primary-color);
    color: white;
    border: none;
    width: 44px;
    height: 44px;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 2px 8px var(--shadow-color);
    transition: all 0.3s ease;
}

.mobile-menu-toggle:hover {
    background: var(--secondary-color);
    transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 1024px) {
    .sidebar {
        width: 260px;
    }

    .main-content {
        margin-left: 260px;
    }
}

@media (max-width: 768px) {
    .mobile-menu-toggle {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .sidebar {
        transform: translateX(-100%);
    }

    .sidebar.open {
        transform: translateX(0);
    }

    .main-content {
        margin-left: 0;
    }

    .content {
        padding: 20px;
    }
}

@media (max-width: 480px) {
    .content {
        padding: 16px;
    }

    .section {
        padding: 16px;
    }

    .sidebar {
        width: 100%;
        max-width: 320px;
    }
}

/* Calendar Styles */
.calendar-container {
    flex: 2;
    min-width: 0;
}

.month-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.month-title {
    font-size: 20px;
    font-weight: 500;
}

.month-header button {
    background: transparent;
    border: none;
    color: var(--primary-color);
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s ease;
}

.month-header button:hover {
    background: rgba(74, 111, 165, 0.1);
}

.weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 8px;
    margin-bottom: 12px;
    text-align: center;
    font-weight: 600;
    color: var(--light-text);
    font-size: 13px;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 8px;
}

.calendar-day {
    aspect-ratio: 1;
    border-radius: 12px;
    border: 1px solid var(--border-color);
    padding: 8px;
    cursor: pointer;
    transition: all 0.3s;
    position: relative;
    display: flex;
    flex-direction: column;
    background: var(--card-bg);
}

.calendar-day:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px var(--shadow-color);
    border-color: var(--primary-color);
}

.calendar-day.selected {
    border: 2px solid var(--primary-color);
    box-shadow: 0 0 0 2px var(--input-focus-shadow);
}

.calendar-day.today .day-number {
    background: var(--primary-color);
    color: white;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.calendar-day.other-month {
    opacity: 0.5;
    background: var(--light-bg);
}

.day-number {
    font-size: 14px;
    font-weight: 500;
    z-index: 1;
    margin-left: auto;
}

.task-count {
    font-size: 12px;
    color: var(--primary-color);
    background: rgba(255, 255, 255, 0.8);
    border-radius: 12px;
    padding: 2px 6px;
    margin-top: auto;
    align-self: flex-end;
}

html.dark-mode .task-count {
    background: rgba(30, 30, 30, 0.8);
}

.tasks-container {
    flex: 1.2;
    min-width: 320px;
    max-width: 450px;
    display: flex;
    flex-direction: column;
    height: 100%;

    background: var(--card-bg);
    padding: 20px 30px;
    border-bottom: 1px solid var(--border-color);
    box-shadow: 0 2px 4px var(--shadow-color);
}

.selected-date {
    font-size: 18px;
    margin-bottom: 15px;
    color: var(--primary-color);
    font-weight: 500;
}

.task-list {
    flex: 1;
    overflow-y: auto;
    margin: 15px 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.task-item {
    background: var(--card-bg);
    border-radius: 10px;
    border: 1px solid var(--border-color);
    padding: 16px;
    display: flex;
    align-items: center;
    gap: 14px;
    border-left: 5px solid;
    box-shadow: 0 2px 5px var(--shadow-color);
    transition: all 0.3s;
}

.task-item:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 10px var(--shadow-color);
}

.task-item.completed .task-text {
    text-decoration: line-through;
    color: var(--light-text);
}

.task-checkbox {
    width: 22px;
    height: 22px;
    border-radius: 6px;
    border: 2px solid var(--border-color);
    cursor: pointer;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
}

.task-checkbox:hover {
    border-color: var(--primary-color);
}

.task-checkbox.checked {
    background: var(--completed-color);
    border-color: var(--completed-color);
    color: white;
}

.task-content {
    flex: 1;
    min-width: 0;
}

.task-text {
    font-size: 16px;
    font-weight: 500;
}

.task-actions {
    display: flex;
    gap: 8px;
}

.task-action-btn {
    background: transparent;
    border: none;
    cursor: pointer;
    color: var(--light-text);
    width: 32px;
    height: 32px;
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
}

.task-action-btn:hover {
    color: var(--primary-color);
    background: rgba(58, 110, 165, 0.1);
}

.task-form {
    display: flex;
    gap: 12px;
    margin-top: auto;
    padding-top: 20px;
    border-top: 1px solid var(--border-color);
}

.task-form .text-input {
    flex: 1;
}

.task-form input[type="color"] {
    padding: 0;
    width: 44px;
    height: 44px;
    border: 1px solid var(--input-border);
    border-radius: 8px;
    cursor: pointer;
    background: transparent;
}

.task-form .btn {
    padding: 0 20px;
}

.color-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.2;
    z-index: 0;
    border-radius: 10px;
    /* Match the parent's border-radius */
}

/* Make task-item in todolist flex and justify content */
.boards .task-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.boards .task-text {
    flex: 1;
}

/* TodoList Page Styles */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 24px;
}












.project-card {
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 10px;
    padding: 25px;
    box-shadow: 0 4px 20px var(--shadow-color);
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
}

.project-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--primary-color), var(--primary-color));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.project-card:hover::before {
    transform: scaleX(1);
}

.project-card:hover {
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 12px 40px var(--shadow-color);
    border-color: var(--primary-color);
}







.project-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.project-card-actions {
    display: flex;
    gap: 4px;
}

/* Floating Action Button */
.fab {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background: var(--primary-color);
    color: white;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    transition: all 0.3s ease;
    z-index: 1050;
}

.fab:hover {
    transform: translateY(-3px) scale(1.05);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.25);
}

/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1100;
    backdrop-filter: blur(5px);
}

.modal-content {
    background: var(--card-bg);
    padding: 30px;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 500px;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
}

.modal-header h2 {
    font-size: 22px;
    color: var(--dark-text);
}

.modal-close-btn {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--light-text);
    transition: color 0.2s ease;
}

.modal-close-btn:hover {
    color: var(--primary-color);
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.project-card-actions .btn {
    padding: 6px;
    background: var(--light-bg);
}

.project-card-info {
    margin-top: 16px;
    font-size: 13px;
    color: var(--light-text);
}

.boards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 24px;
    align-items: flex-start;
}

.board {
    min-height: 200px;
}

.board .task-list {
    min-height: 150px;
    border: 2px  rgba(255, 255, 255, 0);
    border-radius: 8px;
    padding: 8px;
    transition: border-color 0.2s ease;
}

.board .task-list:has(.task-item) {
    border-style: solid;
}

.board .task-list.drag-over {
    border-color: var(--primary-color);
}

/* Settings Page Styles */
.settings-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 24px;
}

.settings-row {
    display: grid;
    grid-template-columns: 150px 1fr;
    align-items: center;
    gap: 20px;
    padding: 16px 0;
    border-bottom: 1px solid var(--border-color);
}

.settings-row:last-child {
    border-bottom: none;
}

.settings-row label {
    font-weight: 500;
    color: var(--light-text);
}

.settings-row .input-group {
    display: flex;
    align-items: center;
    gap: 12px;
}

.settings-row .avatar-preview {
    width: 60px;
    height: 60px;
}

.settings-row input[type="color"] {
    width: 44px;
    height: 44px;
    padding: 2px;
    border-radius: 8px;
    border: 1px solid var(--border-color);
    background: transparent;
}

.color-swatch-container {
    display: flex;
    gap: 8px;
    align-items: center;
}

.color-swatch {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid var(--border-color);
    transition: transform 0.2s ease;
}

.color-swatch:hover {
    transform: scale(1.15);
}

/* Projects Grid */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 24px;
}

.project-card {
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 10px;
    padding: 25px;
    box-shadow: 0 4px 20px var(--shadow-color);
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
}

.project-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--primary-color), var(--primary-color));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.project-card:hover::before {
    transform: scaleX(1);
}

.project-card:hover {
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 12px 40px var(--shadow-color);
    border-color: var(--primary-color);
}

.project-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.project-card-actions {
    display: flex;
    gap: 4px;
}

.project-card-info {
    margin-top: 16px;
    font-size: 13px;
    color: var(--light-text);
}

/* Boards (for potential future use, kept minimal) */
.boards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 24px;
    align-items: flex-start;
}

.board {
    min-height: 200px;
}



/* Task Table Styles */
.task-table-container {
    overflow-y: auto;
}

.task-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 16px;
}

.task-table th,
.task-table td {
    padding: 12px 16px;
    text-align: left;
    border-bottom: 1px solid var(--border-color);
    vertical-align: middle;
}

.task-table th {
    font-weight: 600;
    color: var(--light-text);
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.task-table .task-actions {
    position: relative;
    display: flex;
    justify-content: flex-end;
}

.task-action-menu {
    position: absolute;
    right: 0;
    top: calc(100% + 5px);
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    box-shadow: 0 4px 12px var(--shadow-color);
    z-index: 9999;
    width: 120px;
    padding: 8px;
    display: flex;
    flex-direction: column;
}

.task-action-menu button {
    background: none;
    border: none;
    text-align: left;
    padding: 8px 12px;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;
    color: var(--dark-text);
}

.task-action-menu button:hover {
    background: var(--light-bg);
}

/* Status Select Dropdown */
.status-select {
    padding: 6px 10px;
    border-radius: 6px;
    border: 1px solid var(--border-color);
    background-color: var(--light-bg);
    color: var(--dark-bg);
    font-weight: 500;
    font-size: 13px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
    background-position: right 0.5rem center;
    background-repeat: no-repeat;
    background-size: 1.5em 1.5em;
    padding-right: 2.5rem;
}


