   ========================================================================== */
.project-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 16px;
    margin-bottom: 24px;
}

.project-controls {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
}

.search-bar {
    display: flex;
