    padding: 4px 8px;
    border-radius: var(--border-radius);
    border: 1px solid var(--border-color);
}

.search-bar .text-input {
    border: none;
    background: transparent;
    padding-left: 8px;
}

.search-bar svg {
    color: var(--very-light-text);
}

.sort-controls {
    display: flex;
    align-items: center;
    gap: 8px;
}

.sort-controls .text-input {
    padding: 8px 12px;
}

.create-project-form {
    display: flex;
    gap: 16px;
    margin-bottom: 24px;
}

.create-project-form .text-input {
    flex-grow: 1;
}

.project-card-info p {
    margin: 4px 0;
    font-size: 0.9rem;
    color: var(--light-text);
}

.progress-bar-container {
    width: 100%;
    background-color: var(--light-bg);
    border-radius: 4px;
    height: 8px;
    margin-top: 12px;
    overflow: hidden;
    border: 1px solid var(--border-color);
}

.progress-bar {
    height: 100%;
    background-color: var(--success-color);
    border-radius: 4px;
    transition: width 0.3s ease-in-out;
}

.progress-text {
    font-size: 0.8rem !important;
    text-align: right;
    margin-top: 4px !important;
    color: var(--very-light-text) !important;
}

.timeline-container {
    width: 100%;
    background-color: var(--light-bg);
    border-radius: 4px;
    height: 6px;
    margin-top: 8px;
    overflow: hidden;
    border: 1px solid var(--border-color);
}

.timeline-bar {
    height: 100%;
    background-color: var(--accent-color);
    border-radius: 4px;
    transition: width 0.3s ease-in-out;
}

.finish-date-picker {
    display: flex;
    align-items: center;
    gap: 8px;
}

.finish-date-picker label {
    font-weight: 500;
    font-size: 14px;
    color: var(--light-text);
}

.project-actions-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 16px;
}

.finish-date-picker .text-input {
    padding: 8px 12px;
}

.timeline-section {
    padding: 16px 24px;
}

.timeline-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
}

.timeline-header h4 {
    margin: 0;
    font-weight: 600;
    color: var(--dark-text);
}

.timeline-bar.overdue {
    background-color: var(--danger-color);
}

/* ==========================================================================
   Task Table
   ========================================================================== */

.task-table-container {
    padding: 0;
}

.task-table {
    border-spacing: 0;
}

.task-table td {
    transition: background-color 0.2s ease;
}

.task-table tr:hover td {
    background-color: var(--light-bg);
}

.task-actions-cell {
    display: flex;
    gap: 8px;
    justify-content: flex-end;
}

.task-table .task-action-btn {
    opacity: 0;
    transition: opacity 0.2s ease, background-color 0.2s ease;
}

.task-table tr:hover .task-action-btn {
    opacity: 1;
}

.status-select.status-new {
    border-left: 3px solid #3b82f6;
    color: #3b82f6;
}

.status-select.status-inprogress {
    border-left: 3px solid #f97316;
    color: #f97316;
}

.status-select.status-completed {
    border-left: 3px solid #22c55e;
    color: #22c55e;
}

/* ==========================================================================
   Modal
   ========================================================================== */
.form-group {
    margin-bottom: 16px;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
}

.color-input {
    width: 100%;
    height: 40px;
    padding: 4px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    background-color: var(--input-bg);
    cursor: pointer;
}

/* ==========================================================================
   Responsive
   ========================================================================== */
@media (max-width: 768px) {
    .project-list-header {
        flex-direction: column;
        align-items: stretch;
    }
}

