    border-radius: 8px;
    border-left: 4px solid var(--danger-color);
}

.overdue-list li strong {
    color: var(--dark-text);
}

.overdue-list li span {
    color: var(--light-text);
    font-size: 0.9rem;
}

.empty-state-text {
