.notes-page {
}

.notes-input-area {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}

.note-input {
  width: 100%;
  min-height: 100px;
  padding: 1rem;
  border: 1px solid var(--input-border);
  border-radius: 10px;
  background-color: var(--input-bg);
  color: var(--input-text);
  font-size: 15px;
  resize: vertical;
  box-shadow: 0 2px 6px var(--shadow-color);
  transition: all 0.2s ease;
}

.note-input:focus {
  outline: none;
  border-color: var(--input-focus-border);
  box-shadow: 0 0 8px var(--input-focus-shadow);
}

.btn-add {
  align-self: flex-start;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn-add:hover {
  background: var(--accent-color);
  transform: translateY(-2px);
}

.notes-grid {
  column-count: 3; /* عدد الأعمدة (يمكنك تغييره حسب العرض) */
  column-gap: 1rem; /* المسافة الأفقية بين الأعمدة */
  width: 100%;
}

@media (max-width: 900px) {
  .notes-grid {
    column-count: 2;
  }
}

@media (max-width: 600px) {
  .notes-grid {
    column-count: 1;
  }
}

.note-card {
  display: inline-block; /* ضروري لتفعيل الـ masonry layout */
  width: 100%;
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 14px;
  padding: 1rem;
  margin: 0 0 1rem; /* المسافة الرأسية بين الكروت */
  box-shadow: 0 4px 10px var(--shadow-color);
  transition: all 0.3s ease;
  break-inside: avoid; /* يمنع تقطيع الكارت بين الأعمدة */
}

.note-card:hover {
  transform: translateY(-4px);
  border-color: var(--primary-color);
  box-shadow: 0 6px 14px var(--shadow-color);
}


.note-text {
  font-size: 15px;
  color: var(--dark-text);
  line-height: 1.5;
  margin-bottom: 1rem;
  white-space: pre-wrap;
}

.note-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.note-date {
  font-size: 0.8rem;
  color: var(--very-light-text);
}

.note-buttons {
  display: flex;
  gap: 0.5rem;
}

.note-btn.icon {
  background: none;
  border: none;
  cursor: pointer;
  color: var(--light-text);
  transition: color 0.2s ease;
}

.note-btn.icon:hover {
  color: var(--primary-color);
}

.note-btn.icon.delete:hover {
  color: var(--danger-color);
}

.note-edit-input {
  width: 100%;
  min-height: 80px;
  padding: 0.75rem;
  border-radius: 8px;
  border: 1px solid var(--input-border);
  background-color: var(--input-bg);
  color: var(--input-text);
  resize: vertical;
}

.note-actions {
  margin-top: 0.5rem;
  display: flex;
  gap: 0.5rem;
}

.note-btn.save {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.note-btn.save:hover {
  background: var(--accent-color);
}

.note-btn.cancel {
  background: var(--border-color);
  color: var(--dark-text);
  border: none;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.note-btn.cancel:hover {
  background: var(--light-bg);
}

.empty-state-text {
  text-align: center;
  color: var(--light-text);
  margin-top: 2rem;
}








.copy-toast {
  position: fixed;
  top: 20px;
  right: 20px;
  background: var(--primary-color);
  color: white;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 0.9rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  animation: fadeInOut 2s ease forwards;
  z-index: 1000;
}

@keyframes fadeInOut {
  0% { opacity: 0; transform: translateY(-10px); }
  15% { opacity: 1; transform: translateY(0); }
  85% { opacity: 1; transform: translateY(0); }
  100% { opacity: 0; transform: translateY(-10px); }
}

